<template>
  <v-card flat app>
    <v-toolbar flat dark color="primary" app extended>
      <v-toolbar-side-icon @click.stop="$store.commit('navigationDrawerChange')"></v-toolbar-side-icon>
    </v-toolbar>

    <v-layout row pb-2>
      <v-flex xs8 offset-xs2>
        <v-card class="card--flex-toolbar">
          <v-toolbar card prominent>
            <v-avatar><img src="https://randomuser.me/api/portraits/lego/5.jpg"></v-avatar>
            <v-toolbar-title class="body-2 grey--text">{{ user.username }}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon @click="submitSignout()">
              <v-icon color="pink">exit_to_app</v-icon>
            </v-btn>
          </v-toolbar>
          <v-divider></v-divider>
          <v-card-text>
            <v-layout row pb-2>
              <v-flex>
                : {{ user.first_name }}
              </v-flex>
              <v-flex>
                : {{ user.last_name }}
              </v-flex>
              <v-flex v-if="!!user.class">
                : {{ user.class }}
              </v-flex>
            </v-layout>
            : {{ user.role }}
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-card>
</template>

<script>
import Authentication from '@/components/pages/Authentication'
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState([ 'user' ])
  },

  methods: {
    submitSignout () { Authentication.signout(this, { name: 'Home' }) }
  }
}
</script>

<style>
.card--flex-toolbar {
  margin-top: -80px;
  z-index: 3;
}
</style>



