import axios from '@/store/axios'

const state = {
  test: {},
  currentQuestion: 1
}

const mutations = {
  SET_TEST: (state, test) => { state.test = JSON.parse(JSON.stringify(test)) },

  SET_CURRENT_QUESTION: (state, index) => { state.currentQuestion = index }
}

const actions = {
  getTestByID: ({commit}, id) => {
    axios.get('/api/v1/test/single', { params: { _id: id } })
      .then(({data}) => {
        commit('SET_TEST', data)
      })
  },

  submitTestForCheck (context, test) {
    let testForCheck = test
    //   
    delete testForCheck.title
    delete testForCheck.author
    delete testForCheck.description
    delete testForCheck.publish_date
    delete testForCheck.published
    for (let question of testForCheck.questions) {
      delete question.question
      delete question.question_pack
    }

    axios.post('/api/v1/tester', { test: testForCheck, user: context.rootState.user })
  }
}

const getters = {
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}



