<template>
  <v-card>
    <v-card-title class="headline" v-html="question.question"></v-card-title>

    <v-card-text v-if="question.type === ''">
      <v-checkbox
        v-for="answer in question.answers"
        :key="answer._id"
        :label="answer.answer"
        v-model="answer.correct"
      ></v-checkbox>
    </v-card-text>
  </v-card>
</template>
<script>
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    question: {
      get () { return this.test.questions[this.current_question - 1] }
    },
    ...mapState('tester', {
      test: state => state.test,
      current_question: state => state.currentQuestion
    })
  },

  methods: {
    ...mapMutations({
      'nextQuestion': 'tester/NEXT_QUESTION'
    })
  }
}
</script>



