import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store/index'
import * as Auth from '@/components/pages/Authentication'

// Pages
import Home from '@/components/pages/Home'
import TestEditor from '@/components/pages/TestEditor'
import QuestionPackTable from '@/components/pages/QuestionPackTable'
import Authentication from '@/components/pages/Authentication/Authentication'
import Tester from '@/components/pages/Tester/Tester'
import Profile from '@/components/pages/Authentication/Profile'

// Global components
import Header from '@/components/components/Header'
Vue.component('app-header', Header)

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        requiredAuth: false
      }
    },
    {
      path: '/test/:id',
      name: 'Tester',
      component: Tester
    },
    {
      path: '/test-editor',
      name: 'TestEditor',
      component: TestEditor,
      meta: {
        requiredAuth: true,
        studentAllowed: false
      }
    },
    {
      path: '/question-editor',
      name: 'QuestionEditor',
      component: QuestionPackTable,
      meta: {
        requiredAuth: true,
        studentAllowed: false
      }
    },
    {
      path: '/login',
      name: 'Authentication',
      component: Authentication
    },
    {
      path: '/profile/:id',
      name: 'Profile',
      component: Profile,
      meta: {
        requiredAuth: true
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  store.dispatch('getLoggedUser').then(() => {
    if (to.meta.requiredAuth) {
      if (Auth.default.checkAuthentication()) {
        if (typeof to.meta.studentAllowed !== 'undefined' && !to.meta.studentAllowed && store.state.user.role === '') next({ name: 'Home' })
        else next()
      } else {
        router.push({ name: 'Authentication' })
      }
    } else next()
  })
})

export default router



